.img-splitpeas {
  display: block;
  width: 100%;
  height: auto;
}

.overlay-splitpeas {
  position: absolute;
  width: 90%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  bottom: 10%;
  left: 5%;
  right: 5%;
  opacity: 0;
  transition: 0.5s ease;
  background-color: #abb6a0;
}

.splitpeas {
  position: relative;
  width: 40vw;
}

.splitpeas:hover .img-splitpeas {
  opacity: 0.5;
}

.splitpeas:hover .overlay-splitpeas {
  opacity: 1;
}

